import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Switch,
    Redirect
  } from 'react-router-dom';
import ReactDOM from 'react-dom';
import App2 from './App2';
import Counter from './Counter';
import Funclick from './Funclick';
import Eventbind from './Eventbind';
import Parentcom from './Parentcom';
import Usergreeting from './Usergreeting';
import Namelist from './Namelist';
import Forms from './Forms';
import LifeCycle from './LifeCycle';
import Nav from './Nav';
import Axios from './Axios';
import Bulm from './Bulm';
import Footer from './Footer';
import Boots from './Boots';
import Formsub from './Formsub';
import Fend from './Fend';


class App extends Component {
   
    

    render() {
        return (

            <Router>
                <Nav />
                
            
            <div>
                <div >
                        <Switch>
                             
                            <Route path = "/" exact component = {App2} />
                            <Route path = "/Home" component = {Forms} />   
                            <Route path = "/Funclick" component = {Funclick} /> 
                            <Route path = "/Counter" component = {Counter} /> 
                            <Route path = "/Eventbind" component = {Eventbind} />
                            <Route path = "/Axios" component = {Axios} />
                            <Route path = "/Bulm" component = {Bulm} />
                            <Route path = "/Boots" component = {Boots} />
                            <Route path = "/Formsub" component = {Formsub} />
                            <Route path = "/Fend" component = {Fend} />
                            <Redirect to="/" />
                        </Switch>
                       
                        
                        
                </div>
            </div>
            <Footer />
            </Router>


        );
    }
}

export default App;