import React, { Component } from 'react'

export class Eventbind extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
            msg : "not ok"
             
        }
    }
    
    fun(){

       
            if(this.state.msg == "not ok"){
                this.setState({
                    msg:"ok"
                })
            }
            else{
                this.setState({
                    msg:"not ok"
                })
            }
       

    }

    fun2 = () =>{
        if(this.state.msg == "not ok"){
            this.setState({
                msg:"ok"
            })
        }
        else{
            this.setState({
                msg:"not ok"
            })
        }
    }

    render() {
        return (
            <div>
                <h1>event</h1>
                <h2>{ this.state.msg }</h2>
                <button onClick={this.fun.bind(this)} >event click</button>
                <button onClick={() => this.fun()} >event click</button>
                <button onClick={this.fun2} >event click</button>

            </div>
        )
    }
}

export default Eventbind
