import React, { Component } from 'react'
import Child from './Child';


class Parentcom extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             name : "parent"
        }
    }
    
    gp = () =>{
        alert(`${this.state.name}`)
    }

    render() {
        return (
            <div>
                <Child gp = {this.gp}/>
            </div>
        )
    }
}

export default Parentcom
