import React, { Component } from 'react'

export class Usergreeting extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             log : false    
        }
    }
    



    render() {

        if(this.state.log){
            return (
                <div>
                    
                    <div>you are in !</div>
                </div>
            )
        }
        else{
            return (
                <div>
                    
                    <div>you are out !</div>
                </div>
            )
        }


        
    }
}

export default Usergreeting
