import React, { Component } from 'react'

export class Forms extends Component {

    constructor(props) {
        super(props)
        

        this.state = {
             name: '',
             comment:''
        }
    }
    
    fun = (event) => {
        this.setState({
            name : event.target.value 
        })
    }

    fun2 = (event) => {
        this.setState({
            comment : event.target.value 
        })
    }
    fun3 = (e) => {
        e.preventDefault();
        alert(`${this.state.name} ${this.state.comment}`);
        this.setState({
            name:'',
            comment:''
        })
    }

    render() {
        return (
            <form>
                <div>
                    <label>name</label>
                    <input type='text' value = {this.state.name} onChange = {this.fun} />
                 </div>
                 <div>
                    <label>comment</label>
                    <textarea value = {this.state.comment} onChange = {this.fun2} />
                 </div>
                 <div>
                    <button onClick={this.fun3} >submit</button>
                 </div>

            </form>
            
        )
    }
}

export default Forms
